import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Http, RequestOptionsArgs  } from '@angular/http';
import 'rxjs/add/operator/toPromise';

export class SelectCategory {
  
  level?: number;

  cateId: string;

  cateName: string;
}

/**
 * 三级分类
 */
export class ThreeCategory {
  first?: SelectCategory;
  second?: SelectCategory;
  third?: SelectCategory;
}

@Component({
    selector: 'category-select',
    templateUrl: './category-select.html',
    styleUrls: ['./category-select.scss']
})
export class IhsRulesCategoryComponent implements OnInit {

  constructor(private http: Http) {} 

  @Input()
  url: string = "app/v1/vender/info/category/list/";

  @Output()
  change: EventEmitter<ThreeCategory> = new EventEmitter();

  selected = new ThreeCategory();

  firstCategoryList: SelectCategory[];

  secondCategoryList: SelectCategory[];

  thirdCategoryList: SelectCategory[];

  static emptySelect:SelectCategory[] = [{
      cateId: "-1",
      cateName: "请选择分类"
  }];

  createPromise(path): Promise<any> {
    return this.http.get(this.url + path).map(str => str.json()).toPromise();
  }

  /**
   * 一级分类
   */
  getFirstCategory() {
    this.createPromise("").then(result => {
      if (!result || !result.success) {
        this.printErr(result);
        return;
      }
      this.firstCategoryList = result.body;
      if (this.firstCategoryList && this.firstCategoryList.length > 0) {
        this.selected.first = this.firstCategoryList[0];
        this.getSecondCategory(this.selected.first);
      } else {
        this.secondCategoryList = IhsRulesCategoryComponent.emptySelect;
        this.change.emit(this.selected);
      }
    }, err => {
      this.printErr(err);
    });
  }

  /**
   * 二级分类，参数为选中的一级分类
   */
  getSecondCategory(first: SelectCategory) {
    this.createPromise(first.cateId).then(result => {
      if (!result || !result.success) {
        this.printErr(result);
        return;
      }
      this.secondCategoryList = result.body;
      if (this.secondCategoryList && this.secondCategoryList.length > 0) {
        this.selected.second = this.secondCategoryList[0];
        this.getThirdCategory(first, this.selected.second);
      } else {
        this.thirdCategoryList = IhsRulesCategoryComponent.emptySelect;
        this.change.emit(this.selected);
      }
      
    }, err => {
      this.printErr(err);
    });
  }

  /**
   * 三级分类，参数为选中的一二级分类
   */
  getThirdCategory(first: SelectCategory, second: SelectCategory) {
    this.createPromise(first.cateId + "/" + second.cateId).then(result => {
      if (!result || !result.success) {
        this.printErr(result);
        return;
      }
      this.thirdCategoryList = result.body;
      if (this.thirdCategoryList && this.thirdCategoryList.length > 0) {
        this.selected.third = this.thirdCategoryList[0];
      }
      this.change.emit(this.selected);
    }, err => {
      this.printErr(err);
    });
  }

  /**
   * action below
   * @param event 
   */
  onFirstSelect(event) {
    this.getSecondCategory(this.selected.first);
    this.change.emit(this.selected);
  }

  onSecondSelect(event) {
    this.getThirdCategory(this.selected.first, this.selected.second);
    this.change.emit(this.selected);
  }

  onThirdSelect(event) {
    this.change.emit(this.selected);
  }

  ngOnInit() {
    this.getFirstCategory();
  }

  printErr(err) {
    console.log(err);
  }
}



