import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { Metric } from './metric';

@Component({
    selector: 'total-board-block',
    styleUrls: ['./board-metric-block.scss'],
    template: `
    <div *ngIf="metrics" class="row">
        <div *ngFor="let m of metrics;" class="total-metrics total-metrics-all">
        <span *ngIf="m.text">
            <nz-tooltip [nzTitle]="m.text">
                <span nz-tooltip>{{m.name}}</span>
            </nz-tooltip>
        </span>
        <span *ngIf="!m.text">{{m.name}}</span>
        </div>
    </div>
    <div *ngIf="metrics" class="row">
        <div *ngFor="let metric of metrics; let i = index" class="total-metrics">
            <board-total-sub-metric [metric]="metric" [index]="i" [label]="'block'" (changeBlock)="changeBlock($event)"></board-total-sub-metric>
        </div>
    </div>
    `
})
export class TotalBoardBlock implements OnInit {

    @Input()
    metrics: Metric[];

    @Output()
    onselect: EventEmitter<Metric> = new EventEmitter();

    changeBlock(val: Metric) {
        this.onselect.emit(val);
    }

    ngOnInit() {
    }
}



