import {Component, Input, Output, EventEmitter, Host, Inject, forwardRef, OnInit, OnChanges, DoCheck, SimpleChange} from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import { NzNotificationService } from 'ng-zorro-antd';

import { YscDataTablesConfig, YscDataTableColumn } from './ysc-datatables-config';
//templateUrl: './ysc-table.html',
@Component({
  selector: 'ysc-table',
  styles:[`
  .table-center-cell {
    text-align:center;
  }
  .table-right-cell {
    text-align:right;
  }
  `],
  templateUrl: './ysc-table.html',
  exportAs: "DataTables",
  providers:[]
})
export class YscDataTables implements OnInit, OnChanges, DoCheck {

  constructor(private http: Http, private _notification: NzNotificationService) {
  }

  /**
   * 用在method，isEditable 参数
   */
  @Input("config")
  public config: YscDataTablesConfig;

  @Input("caller")
  private caller: Component;

  @Input()
  public params;

  @Input("isPagination")
  private isPagination: boolean;

  @Input()
  private nzSize;

  @Input("isDynamic")
  private isDynamic: boolean;

  /**
   * 字段选择组件选择当前字段, 包含type为value(包含默认空)的所有列
   */
  public colSelect = {};

  /**
   * 来源于config
   */
  private url: string;

  private columns;

  private selectedColumns;

  private state: 'INIT'|'LOADING'|'FINISHED';

  private primaryKey;

  /*全选相关 */
  private allChecked;

  private indeterminate;
  //选中标记
  private checkFlagMap = {};

  /**
   * 以key标识，只接受remote
   * 值是根据data来的数组，标识当前列的rowspan情况，占用列的值
   */
  private rowspanList: number[] = [];
  //哪些列是可以span的 (column index => key or render function)
  private spanColSet = {};

  public page: {
    start: number, /*起始数据下标*/
    pageSize: number, /*页面大小*/
    total?: number, /*总数据*/
    order?: "DESC"|"desc" /*排列方式，默认asc，有值 desc*/
  };

  private _pageNo = 1;

  private data: any[] = [];

  /**
   * 样式相关
   */
  private _nzIsPagination;

  private nzScrollX: string;

  private flapParam(param?, page?) {
    let _param = this.deepCopy(param);
    for(let k in _param) {
      if (_param[k] === '') {
        delete _param[k];
      }
    }
    if (page) {
      _param['start'] = page.start;
      _param['pageSize'] = page.pageSize;
      _param['total'] = page.total;
      _param['order'] = page.order;
    }
    return _param;
  }

  /**
   * 按当前参数刷新表格
   */
  public reload() {
    this.queryDataList(this.params, this.page);
  }
  /**
   * 按传入参数刷新表格
   */
  public reloadByParams(params) {
    this.queryDataList(params, this.page);
  }

  /**
   * 设置当前可以显示的列，参数为列的key
   */
  public setSelectedColumn(columns: Array<YscDataTableColumn>) {
    let keySet = new Set<string>();
    for (let i in columns) {
      keySet.add(columns[i].key);
    }
    for (let i in this.colSelect) {
      let key = this.columns[i].key;
      this.colSelect[i] = keySet.has(key);
    }
    this.refreshSelectColumns();
  }

  /**
   *
   * @param start 数据的起始下标
   */
  public setPageStart(start) {
    this.page.start = start || 0;
    this._pageNo = 1;
    //this.queryDataList(this.params, this.page);
  }

  /**
   * @warning： <br>
   * <strong>请不要使用该方法刷新页面，可以使用reload 和 setPageStart来刷新列表数据</strong>
   * @param param
   * @param page
   */
  public queryDataList(param?, page?) {
    // 如果是动态添加数据
    if (this.isDynamic){
      this.selectedColumns = this.columns;
    }
    let promise: Promise<any>;
    this.state = 'LOADING';
    if (!this.config.method || this.config.method === 'GET') {
      promise = this.http.get(this.config.url, {
        params: this.flapParam(param, page)
      }).map(res => res.json()).toPromise();
    } else {
      promise = this.http.post(this.config.url, this.flapParam(param, page)).map(res => res.json()).toPromise();
    }
    promise.then(result => {
      this.page.total = 0;
      if (!result) {
        this.clearData();
        this.printError("查询列表数据时，系统繁忙，请稍后再试！");
        return;
      }
      if (!result.success || !result.body) {
        this.clearData();
        this.printError(result.message);
        return;
      }
      this.resetCheckList();
      if (result.body.list) {
        let resultList = this.setRowSpanList(result.body.list);
        this.data = resultList;
        // 如果是动态添加数据
        if (this.isDynamic){
          for (let i = 0; i < this.data.length; i++) {
            if (this.data[i].paramInfo && this.data[i].paramInfo.indexOf('\\') < 0) {
              let field = JSON.parse(this.data[i].paramInfo);
              for (let m in field) {
                this.data[i][m] = field[m];
              }
            }
          }
        }
      }
      if (result.body.page) {
        this.page.total = result.body.page.total;
      }

      this.state = 'FINISHED';

    }, err => {
      this.printError("查询列表数据时，系统繁忙，请稍后再试！");
      this.page.total = 0;
      this.state = 'FINISHED';
    });
  }

  private clearData(): void {
    this.data = [];
    this.page.total = 0;
    this.page.start = 0;
  }

  /**
   * getter
   */
  public getData() {
    return this.data;
  }

  public getPage() {
    return this.page;
  }

  public getCheckedList() {
    let checkList = new Array<any>();
    let hasSpan = false;
    if (this.rowspanList && this.rowspanList.length == this.data.length) {
      hasSpan = true;
    }
    for (let key in this.checkFlagMap) {
      if (this.checkFlagMap[key]) {
        if (hasSpan || this.rowspanList[key] > 1) {
          for (let start = 0; start < this.rowspanList[key]; start++) {
            checkList.push(this.data[(+key) + start]);
          }
        } else {
          checkList.push(this.data[key]);
        }
      }
    }
    return checkList;
  }

  private resetCheckList() {
    this.checkFlagMap = {};
    this.refreshCheckState(false);
  }

  private deepCopy(p) {
    if (!p) {
      return null;
    }
    let c = (p.constructor === Array) ? [] : {};
    for (let i in p) {
      if (! p.hasOwnProperty(i)) {
        continue;
      }
      if (typeof p[i] === 'object') {
        c[i] = this.deepCopy(p[i]);
      } else {
        c[i] = p[i];
      }
    }
    return c;
  }

  /** 目前只能更新非自定义render的值，所以直接取column.key
   * update url为 $url/primaryKey
   * @param value 改变的 columbn值
   * @param item 当前行的数据
   * @param column 当前列的定义
   */
  private updateItem(value, item, column) {
    if (this.config.beforeUpdateCheck) {
      let copy = this.deepCopy(item);
      if (column.key) {
          copy[column.key] = value;
      }
      let checkResult = this.config.beforeUpdateCheck.call(this.caller, copy);
      if (!checkResult) {
        value = item[column.key];
        this.printError("更新的数据未通过校验");
        return;
      }
      if (checkResult.success) {
        Object.assign(item, copy);
        value = item[column.key];
      } else {
        value = item[column.key];
        this.printError(checkResult.message);
        return;
      }
    }

    if (this.config.beforeUpdatePromise) {
      let copy = this.deepCopy(item);
      if (column.key) {
          copy[column.key] = value;
      }
      let checkResultPromise = this.config.beforeUpdatePromise.call(this.caller, copy);
      checkResultPromise.then(checkResult=>{
        if (!checkResult) {
          value = item[column.key];
          this.printError("更新的数据未通过校验");
          return;
        }
        if (checkResult.success) {
          Object.assign(item, copy);
          value = item[column.key];
        } else {
          value = item[column.key];
          this.printError(checkResult.message);
          return;
        }
        this._updateItem(value, item, column);
        return;
      }, err => {
        value = item[column.key];
        this.printError("更新的数据未通过校验");
        return;
      });
    } else {
      this._updateItem(value, item, column);
    }

  }

  private _updateItem(value, item, column) {

    if (!this.config.url) {
      throw new Error("url为空");
    }
    let url;
    if (!this.config.url.endsWith("/")) {
      url = this.config.url + "/";
    } else {
      url = this.config.url;
    }
    let tmp = item[column.key];
    item[column.key] = value;

    let updateItem = this.deepCopy(item);
    /**
     * 动态列
     */
    if (this.isDynamic){
      if (updateItem['paramInfo'] && updateItem['paramInfo'].indexOf('\\') < 0) {
        let param = JSON.parse(updateItem['paramInfo']);
        for (let k in param) {
          if (updateItem.hasOwnProperty(k)) {
            param[k] = updateItem[k];
            delete updateItem[k];
          }
        }
        updateItem['paramInfo'] = JSON.stringify(param);
      }
    }
    let promise: Promise<any>;
    promise = this.http.post(url + item[this.primaryKey], updateItem).map(res => res.json()).toPromise();
    promise.then(result => {
      if (!result) {
        this.printError("更新失败");
        value = tmp;
        item[column.key] = tmp;
        return;
      }
      if (!result.success) {
        this.printError(result.message || "更新失败");
        value = tmp;
        item[column.key] = tmp;
        return;
      }
      this._notification.success("成功", result.message);
      this.reload();
    }, err => {
      this.printError("更新失败");
      value = tmp;
      item[column.key] = tmp;
    });
  }

  /**
    全选相关
   */
  private checkAll(value: boolean) {
    this.indeterminate = false;
    for (let index in this.data) {
      this.checkFlagMap[index] = value;
    }
  }

  /**
   *
   * @param value 只影响全选checkbox
   */
  private refreshCheckState(value: boolean) {

    let dataSize = this.data ? (this.data.length || 0) : 0;
    let size = 0;
    let allTrue = true;
    let allFalse = true;
    for (let key in this.checkFlagMap) {
      size += 1;
      if (this.checkFlagMap[key]) {
        allFalse = false;
      } else {
        allTrue = false;
      }
    }
    if (size == 0) {
      this.allChecked = false;
      this.indeterminate = false;
      return;
    }
    if (size < dataSize) {
      this.allChecked = false;
      this.indeterminate = true;
      return;
    }
    this.allChecked = allTrue;
    this.indeterminate = (allFalse && allTrue) || (!allFalse && !allTrue);
    return;
  }

  private displayDataChange(value) {


  }

  pageSizeChange(pageSize) {
    if (pageSize) {
      this.page.pageSize = pageSize;
      this.page.start = (this._pageNo - 1) * this.page.pageSize;
      this.checkFlagMap = {};
      this.queryDataList(this.params, this.page);
    }
  }

  pageIndexChange(pageIndex) {
    if (pageIndex && pageIndex >= 1) {
      this._pageNo = pageIndex;
      this.page.start = (pageIndex - 1) * this.page.pageSize;
      this.checkFlagMap = {};
      this.queryDataList(this.params, this.page);
    }
  }

  public getColSelectMap(): Object {
    return this.colSelect;
  }

  public setColSelectMap(result: Object) {

    for (let key in this.colSelect) {
      this.colSelect[key] = result[key];
    }
    this.refreshSelectColumns();
  }

  /**
   * selectedColumns包含所有在列表显示的列
   */
  private refreshSelectColumns() {
    this.selectedColumns = new Array<YscDataTableColumn>();

    for (let k in this.columns) {
      if (this.columns[k].type && this.columns[k].type != 'value') {
        this.selectedColumns.push(this.columns[k]);
      } else {
        if (this.colSelect[k]) {
          this.selectedColumns.push(this.columns[k]);
        }
      }
    }

  }
  public getAllColumns() {
    return this.columns;
  }

  public getColSelectList():Array<YscDataTableColumn> {

    return this.selectedColumns;
  }

  /**
   * lifecycle on init
   */
  ngOnInit(): void {
    /**
     * init the component
     */

    this.page = {
      start: 0,
      pageSize: 10,
      total: 0
    };
    if (this.config.pageSize) {
      this.page.pageSize = this.config.pageSize;
    } else {
      this.page.pageSize = 10;
    }

    this.columns = this.config.columns;
    this.state = 'INIT';

    if (typeof this.isPagination === 'undefined') {
      this.isPagination = true;
    }

    /*
     * init default scroll
     */
    if(this.config.nzScrollX == null) {
        if (this.columns.length <= 7) {
            this.nzScrollX = '100%';
        } else {
            this.nzScrollX = '1024';
        }
    }else{
        this.nzScrollX = this.config.nzScrollX;
    }

    /**
     * check config validation
     */

    let checkboxCount = 0;
    let primaryCount = 0;
    let hasSpan = false;
    for (let i in this.columns) {
      if (this.columns[i].type === 'checkbox') {
        checkboxCount += 1;
      }
      if (this.columns[i].primary) {
        primaryCount += 1;
        this.primaryKey = this.columns[i].key;
      }
      if (this.columns[i].isScalable) {
        if (this.columns[i].key) {
          this.spanColSet[i] = this.columns[i].key;
        } else if (this.columns[i].render) {
          this.spanColSet[i] = this.columns[i].render;
        }
        hasSpan = true;
      }
    }
    if (hasSpan) {
      this.rowspanList = [];
    }
    if (checkboxCount > 1) {
      throw new Error("初始化失败，选中框只能有一个");
    }
    if (primaryCount > 1) {
      throw new Error("初始化失败，作为主键的列只能有一个");
    } else if (primaryCount === 0) {
      this.primaryKey = "id";
    }
    for (let i in this.columns) {
      if (!this.columns[i].type || this.columns[i].type == 'value') {
        this.colSelect[i] = i;
      }
    }
    this.refreshSelectColumns();

    if (!this.params) {
      this.params = {};
    }

    /**
     * loading data
     */
    if (this.config.url) {
      this.setPageStart(0);
      if (typeof this.isPagination === 'undefined') {
        this.isPagination = true; // 后端分页
      }
    } else if (this.config.localData) {
      this.data = this.config.localData
      this.page.total = this.data.length;
      this.state = 'FINISHED';
      this.isPagination = false; // 前端分页
    } else {
      throw new Error("初始化失败，没有查询路径或本地数据");
    }
  }

  /**
   * 仅remote模式，可以span
   */
  private setRowSpanList(data: any[]): any[] {

    if (!data) {
      return data;
    }
    if (!this.spanColSet) {
      return data;
    }
    let spanColSize = 0;
    for (let h in this.spanColSet) {
      if (this.spanColSet[h]) {
        spanColSize += 1;
      }
    }
    if (!spanColSize) {
      return data;
    }

    let rowspanList = [];
    let p = 0;
    for (let i = 0; i < data.length; i++) {
      if (rowspanList.length < i + 1) {
        rowspanList[i] = 0;
      }
      let allSame = true;
      for (let k in this.spanColSet) {
        if (this.columns[k].key) {
          if (data[p][this.spanColSet[k]] != data[i][this.spanColSet[k]]) {
            allSame = false;
            break;
          }
        } else if (this.columns[k].render) {
          let v1 = this.columns[k].render(data[p]);
          let v2 = this.columns[k].render(data[i]);
          if (v1 != v2) {
            allSame = false;
            break;
          }
        }
      }
      if (!allSame) {
        p = i;
      }
      rowspanList[p] += 1;
    }
    this.rowspanList = rowspanList;
    return data;
  }

  ngOnChanges(changes: {[key: string]: SimpleChange}): void {

  }

  ngDoCheck(): void {
  }

  ngAfterViewInit() {
  }

  printError(msg) {
    this._notification.error("失败", msg);
  }
}



