
// const host = 'http://ysc.jd.com';
const host = '';
// const host = 'http://sas.jd.com';

export const CONFIG = {
  URL: {
    /* ----------- 请求链接拼接地址 ------------------
    * 默认为 GET 请求
    *--------------------------------------------*/

    /*----------- 门户首页接口 -----------*/
    // 获取顶部商家列表
    shoplist: host + '/auth-rest/eclpMasterSellers/sellerlist',
    // 获取公告
    notice: host + '/portal/notice',
    // 获取用户信息(pin, deptNo等)
    userInfo: host + '/portal/getUserInfo',
    // 获取事业部 {userPin:用户pin}
    dept: host + '/portal/getDeptInfo',
    // 获取滞销处理 {sellerNo:卖家编号}
    returnSuggest: host + '/portal/getReturnSuggest',
    // 库存指标 {deptNo:事业部编号 sellerNo:卖家编号}
    ihsKPI: host + '/portal/getIhsKPI',
    // 补货指标 {deptNo:事业部编号}
    pbsKPI: host + '/portal/getPbsKPI',
    // 滞销指标 {sellerNo:卖家编号}
    returnKPI: host + '/portal/getReturnKPI',
    // 获取销量计划 {userPin:用户pin}
    salePlan: host + '/portal/getSalePlan',
    // 当周销售计划完成率 {deptNo:事业部编号}
    salePlanForWeek: host + '/portal/getSalePlanForWeek',
    // GMV信息
    /*{
      deptNo:事业部编号
      sellerNo:卖家编号
      dateGran:时间维度("week"、"day"、"month")
    }*/
    gmv: host + '/portal/getGmvInfo',
    logout: host + "/auth-rest/logout"
  }
};



