import {Component, Input, Output, EventEmitter, Injectable} from '@angular/core';
import { DatePipe } from '@angular/common';
import { NgbDatepickerI18n, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

const I18N_VALUES = {
    weekdays: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
    months: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
  }

@Injectable()
export class CustomDatepickerI18n extends NgbDatepickerI18n {

  getWeekdayShortName(weekday: number): string {
    return I18N_VALUES.weekdays[weekday - 1];
  }
  getMonthShortName(month: number): string {
    return I18N_VALUES.months[month - 1];
  }
  getMonthFullName(month: number): string {
    return this.getMonthShortName(month);
  }
}

@Component({
  selector: 'ngbd-datepicker-popup',
  templateUrl: './datepicker-popup.html',
  providers:[{provide: NgbDatepickerI18n, useClass:CustomDatepickerI18n}]
})
export class NgbdDatepickerPopup {
  
  public set model(value) {
      this.change.emit(new Date(value.year, value.month - 1, value.day));
  }
  
  @Input()
  startDate: Date;
  @Input()
  minDate: Date;
  @Input()
  maxDate: Date;

  constructor() { }

  @Output()
  change: EventEmitter<Date> = new EventEmitter();

  _minDate:NgbDateStruct;
  _maxDate:NgbDateStruct;
  _startDate:NgbDateStruct;

  ngOnInit() { 
    if (!this.startDate) {
      this.startDate = new Date();
    }
    this._startDate = { day: this.startDate.getDate(), month: this.startDate.getMonth() + 1, year: this.startDate.getFullYear()};
    this.model = this._startDate;
    if (!this.minDate) {
      let d = new Date(this.startDate);
      d.setMonth(d.getMonth() - 1);
      this._minDate = { day: d.getDate(), month: d.getMonth() + 1, year: d.getFullYear()};
    } else {
      this._minDate = { day: this.minDate.getDate(), month: this.minDate.getMonth() + 1, year: this.minDate.getFullYear()};
    }
    if (!this.maxDate) {
        this._maxDate = { day: this.startDate.getDate(), month: this.startDate.getMonth() + 1, year: this.startDate.getFullYear()};
    } else {
      this._maxDate = { day: this.maxDate.getDate(), month: this.maxDate.getMonth() + 1, year: this.maxDate.getFullYear()};
    }
   }
}


