import {
  Component, Input, Output, EventEmitter,
  OnInit, OnChanges, DoCheck, SimpleChange,
  SimpleChanges
} from '@angular/core';

import { YscDataTables } from './ysc-datatables.component';

@Component({
  template: `
  <br/>
  <span class="pull-right">
    <ul class="ant-pagination" *ngIf="dataLength > 0">
      <li class="page-item" *ngIf="activePage > 1" [class.disabled]="activePage <= 1" (click)="setPage(1)">
          <a class="page-link" style="cursor: pointer">首页</a>
      </li>
      <li class="ant-pagination-prev" *ngIf="activePage > 1" [class.disabled]="activePage <= 1" (click)="setPage(activePage - 1)">
          <a class="page-link" style="cursor: pointer">上一页</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage > 4 && activePage + 1 > lastPage" (click)="setPage(activePage - 4)">
          <a class="page-link" style="cursor: pointer">{{activePage-4}}</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage > 3 && activePage + 2 > lastPage" (click)="setPage(activePage - 3)">
          <a class="page-link" style="cursor: pointer">{{activePage-3}}</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage > 2" (click)="setPage(activePage - 2)">
          <a class="page-link" style="cursor: pointer">{{activePage-2}}</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage > 1" (click)="setPage(activePage - 1)">
          <a class="page-link" style="cursor: pointer">{{activePage-1}}</a>
      </li>
      <li class="ant-pagination-item active">
          <a class="page-link" style="cursor: pointer">{{activePage}}</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage + 1 <= lastPage" (click)="setPage(activePage + 1)">
          <a class="page-link" style="cursor: pointer">{{activePage+1}}</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage + 2 <= lastPage" (click)="setPage(activePage + 2)">
          <a class="page-link" style="cursor: pointer">{{activePage+2}}</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage + 3 <= lastPage && activePage < 3" (click)="setPage(activePage + 3)">
          <a class="page-link" style="cursor: pointer">{{activePage+3}}</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage + 4 <= lastPage && activePage < 2" (click)="setPage(activePage + 4)">
          <a class="page-link" style="cursor: pointer">{{activePage+4}}</a>
      </li>
      <li class="ant-pagination-next" *ngIf="activePage + 1 <= lastPage" [class.disabled]="activePage >= lastPage" (click)="setPage(activePage + 1)">
          <a class="page-link" style="cursor: pointer">下一页</a>
      </li>
      <li class="ant-pagination-item" *ngIf="activePage + 1 <= lastPage" [class.disabled]="activePage >= lastPage" (click)="setPage(lastPage)">
          <a class="page-link" style="cursor: pointer">尾页</a>
      </li>
    </ul>
    <span class="ant-pagination" *ngIf="dataLength > 0">
      &nbsp;&nbsp;&nbsp;
      <input class="page-item" [(ngModel)]="selectPage" style="width: 35px"/>&nbsp;
      <a class="page-link" (click)="setPage(selectPage)" style="cursor: pointer">跳转</a>
    </span>

    <div class="ant-pagination-options">
    <nz-select
      class="ant-pagination-options-size-changer"
      [ngModel]="rowsOnPage"
      (ngModelChange)="pageSizeChange($event)">
      <nz-option
        *ngFor="let option of _options"
        [nzLabel]="option+ '条/页'"
        [nzValue]="option">
      </nz-option>
    </nz-select>
    <div class="ant-pagination-options-quick-jumper">
      跳至<input [ngModel]="nzPageIndex" (ngModelChange)="nzPageIndexChange($event)">页
    </div>
  </div>
  </span>
  
  `,
  selector: 'ysc-paginator',
  providers:[]
})
export class YscPaginator implements OnInit, OnChanges, DoCheck {

  @Input("datatables")
  datatables: YscDataTables;

  activePage: number;

  lastPage: number;

  dataLength:number;
  rowsOnPage:number;

  _options = [5,10,20,50]

  private setRowsOnPage(rows: number): void {
    this.rowsOnPage = rows;
  }

  private setPage(pageNo:number) {
    if (!pageNo) {
      return;
    }
    if (pageNo <= 1) {
      this.activePage = 1;
    }
    else if (this.lastPage < pageNo) {
      this.activePage = this.lastPage;
    } else {
      if (typeof pageNo === 'string') {
        this.activePage = parseInt(pageNo);
      } else {
        this.activePage = pageNo;
      }
    }

    let start = 0;
    if (pageNo >= 1) {
      start = (pageNo - 1) * this.rowsOnPage;
    }
  }

  /**
   * 根据关联表格，刷新分页信息
   */
  private refreshPage() {

    if (this.datatables.getData()) {
      this.dataLength = this.datatables.getData().length;
    } else {
      this.dataLength = 0;
    }
    if (this.datatables.getPage() && this.datatables.getPage().pageSize) {
      this.rowsOnPage = this.datatables.getPage().pageSize;
    }
    if (this.datatables.getPage()) {
      let total = this.datatables.getPage().total || 0;
      if (total % this.rowsOnPage === 0) {
        this.lastPage = Math.floor(total / this.rowsOnPage);
      } else {
        this.lastPage = Math.floor(total / this.rowsOnPage) + 1;
      }
    }
  }

  pageSizeChange(event) {

  }

  nzPageIndexChange(event) {
    
  }

  ngOnInit(): void {
    this.activePage = 1;
    this.rowsOnPage = 10;
    this.lastPage = 1;
    this.refreshPage();
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  ngDoCheck(): void {
    this.refreshPage();
  }
}



