import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { Metric } from './metric';

@Component({
    selector: 'board-total-metric',
    styleUrls: ['./board-metric-block.scss'],
    templateUrl: './board-metric-block.html'
})
export class BoardTotalMetricComponent implements OnInit {

    @Input()
    metric: Metric;

    @Input()
    label:string;

    @Output()
    click:EventEmitter<Metric> = new EventEmitter();
    
    onClick() {
        this.click.emit(this.metric);
    }
    ngOnInit() {
    }
    
}

@Component({
    selector: 'board-total-sub-metric',
    styleUrls: ['./board-metric-block.scss'],
    templateUrl: './board-metric-sub-block.html'
})
export class BoardTotalSubMetricComponent implements OnInit {

    @Input()
    metric: Metric;

    @Input()
    index:number;

    @Input()
    label:string;

    @Output()
    changeBlock: EventEmitter<Metric> = new EventEmitter();

    changeRadio() {
        this.changeBlock.emit(this.metric);
    }

    ngOnInit() {
    }
}



