import {Component, Input, Output, EventEmitter, OnInit, OnChanges, DoCheck, SimpleChange} from '@angular/core';

import { YscDataTables } from './ysc-datatables.component';

@Component({
  selector: 'ysc-datatables-btn',
  template: `
    <button nz-button [nzType]="'primary'" (click)="clickEmitter($event)">{{btnName}}</button>
  `,
  providers:[]
})
export class YscDataTablesBtn {

  @Input("datatables")
  datatables: YscDataTables;

  @Input("name")
  btnName: string;

  @Output("onclick")
  onclick: EventEmitter<any> = new EventEmitter();

  ngOnInit(): void {

  }

  public clickEmitter(event) {

    this.onclick.emit(this.datatables.getCheckedList());

  }

}



