import {Component, Input, Output, EventEmitter, OnInit, OnChanges, DoCheck, SimpleChange} from '@angular/core';

import { NzModalService } from 'ng-zorro-antd';

import { YscDataTables } from './ysc-datatables.component';
import { YscDataTableColumn } from './ysc-datatables-config';

@Component({
  selector: 'ysc-datatables-colselect',
  styles: [`
  .colselect {
    color:rgb(95, 167, 250);
    font-size: 14px;
  }
  .colselect:hover {
    cursor: pointer;
  }
  .colcheckbox {
      display:inline-block;
      text-align:left;
      width: 33%;
      white-space:nowrap;
  }
  `],
  template: `
    <label class="colselect" (click)="showColSelectModal(title, content, footer)">字段设置</label>
    <ng-template #title>
        <span>字段设置</span>
    </ng-template>
    <ng-template #content>
        <span *ngFor="let item of colList; let i = index" [ngClass]="{'colcheckbox': !item.type || item.type == 'value' }">
            <label nz-checkbox *ngIf="!item.type || item.type == 'value'" [(ngModel)]="colSelectMap[i]">
                <span>{{item.header}}</span>
            </label>
        </span>
    </ng-template>
    <ng-template #footer>
        <div>
        <button nz-button [nzType]="'primary'" [nzSize]="'large'" (click)="colSelect($event)" [nzLoading]="isConfirmLoading">
            确认选择
        </button>
        <button nz-button [nzSize]="'large'" (click)="cancelColSelect($event)">
            取消
        </button>
        </div>
    </ng-template>
  `,
  providers:[]
})
export class YscDataTablesColSelect {

  constructor(private modalService: NzModalService){}

  currentModal;
  isConfirmLoading = false;

  colList;
  colSelectMap: Object;

  @Input("datatables")
  datatables: YscDataTables;

  @Output()
  notifySelected: EventEmitter<Array<YscDataTableColumn>> = new EventEmitter<Array<YscDataTableColumn>>();

  ngOnInit(): void {
      if (!this.datatables) {
          throw new Error("初始化有误，没有对应的表格");
      }
      
  }
    
  
  showColSelectModal(titleTpl, contentTpl, footerTpl) {
    //所有非checkbox列（index, boolean）
    this.colList = this.datatables.getAllColumns();
    let selectMap = this.datatables.getColSelectMap();
    this.colSelectMap = {};
    for (let i in selectMap) {
        this.colSelectMap[i] = selectMap[i];
    }

    let datatables = this.datatables;
    let emitter = this.notifySelected;
    let localSelectMap = this.colSelectMap;
    this.currentModal = this.modalService.open({
        title       : titleTpl,
        content     : contentTpl,
        footer      : footerTpl,
        maskClosable: false,
        onOk() {
            if (emitter) {
                datatables.setColSelectMap(localSelectMap);
                emitter.emit(datatables.getColSelectList());
            }
        },
        onCancel() {}
      });
  }

  colSelect(e) {
      if (this.currentModal) {
        this.isConfirmLoading = true;
        this.currentModal.destroy('onOk');
        this.isConfirmLoading = false;
        this.currentModal = null;
      }
  }

  cancelColSelect(e) {
    if (this.currentModal) {
        this.currentModal.destroy('onCancel');
        this.currentModal = null;
    }
  }

}


