import { Injectable } from '@angular/core';
import { Http, Headers, Response, URLSearchParams, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/toPromise';

import { CONFIG } from './config';

@Injectable()
export class DataService {

  constructor(private http: Http) { }

  // 通用获取 json 字符串方法
  public getJson(URL, params = null): Promise<any> {
    const url = params == null ? URL : URL + '?' + this.stringify(params);
    return this.http.get(url).toPromise()
      .then(res => res.json())
      .catch(this.handleErrorPromise);
  }

  public postForm(url, body) {
    const bodyString = this.createSearchParams(body).toString();
    return this.http.post(url, bodyString,
      {
        headers: new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' }),
        withCredentials: true
      })
      .toPromise().then(res => res.json());
    // .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
  }

  public postJson(url, body) {
    return this.http.post(url, JSON.stringify(body),
      new RequestOptions({
        headers: new Headers({ 'Content-Type': 'application/json' })
      })).toPromise().then(res => res.json());
  }

  // obj转为querystring
  public stringify(obj) {
    const string = [];
    if (!!obj && obj.constructor === Object) {
      for (const prop in obj) {
        if (obj[prop] instanceof Array) {
          for (let i = 0, length = obj[prop].length; i < length; i++) {
            string.push([prop, obj[prop][i]].join('='));
          }
        } else {
          string.push([prop, obj[prop]].join('='));
        }
      }
    }
    return string.join('&');
  }

  // 将两个json合并为一个json
  public mergeJson(obj1, obj2) {
    const obj = {};
    for (let attr in obj1) {
      obj[attr] = obj1[attr];
    }
    for (let attr in obj2) {
      obj[attr] = obj2[attr];
    }
    return obj;
  }

  private handleError(error: any) {
    const errMsg = (error.message) ? error.message :
      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg); // log to console instead
    return Observable.throw(errMsg);
  }

  private handleErrorPromise(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  private createSearchParams(data: any): URLSearchParams {
    const searchParams = new URLSearchParams();
    for (const key in data) {
      if (data.hasOwnProperty(key) && data[key]) {
        searchParams.append(key, String(data[key]));
      }
    }
    return searchParams;
  }

  public getJsonNoCache(url) {
    let headersAdditional = new Headers();
    headersAdditional.append('Cache-control', 'no-cache');
    headersAdditional.append('Cache-control', 'no-store');
    headersAdditional.append('Expires', '0');
    headersAdditional.append('Pragma', 'no-cache');
    const param = {headers: headersAdditional};
    return this.http.get(url, param).toPromise();
  }

}



