export class Metric {

    //指标key
    key: string;

    //指标名
    name: string;

    //显示标题
    title?: string;

    text?: string;

    //指标值
    value?: string;

    //指标环比
    radio?: number;

    //是否有副指标
    isSubMetric?: boolean;

    //副指标
    subMetricName?: string;

    //副指标值
    subMetricValue?: string;

    //指标等级
    level?: number;

    //指标单位
    unit?: string;

    //是否入仓
    storeType?: number;

    //指标是否可用公式表示
    isFormula?: boolean;

    //公式
    formulaExp?: string;

    //指标是否可显示
    showIndirection?: boolean;

    //指标健康状况, 0，健康，1 不健康
    healthType?: number;



}



