class Option {
    data ?: Array<number[]> | number[];
    datasets : Array<any>;
    labels ?: Array<any>;
    chartType?: string;
    options ?: any;
    colors ?: Array<any>;
    legend?: boolean;
    chartClick(event) : any {};
    chartHover(event): any {};

    constructor() {
        this.legend = false;
        this.colors = [{
            backgroundColor: 'rgb(159,202,252)',
            borderColor: 'rgb(159,202,252)',
            pointBackgroundColor: '#fff',
            pointBorderColor: 'rgb(159,202,252)',
            pointHoverBackgroundColor: '#fff',
            pointHoverBorderColor: 'rgb(159,202,252)'
        },{
            backgroundColor: 'rgb(41,140,230)',
            borderColor: 'rgb(41,140,230)',
            pointBackgroundColor: '#fff',
            pointBorderColor: 'rgb(41,140,230)',
            pointHoverBackgroundColor: '#fff',
            pointHoverBorderColor: 'rgb(41,140,230)'
        },
            {
                backgroundColor: 'rgb(155,214,96)',
                borderColor: 'rgb(155,214,96)',
                pointBackgroundColor: '#fff',
                pointBorderColor: 'rgb(155,214,96)',
                pointHoverBackgroundColor: '#fff',
                pointHoverBorderColor: 'rgb(155,214,96)'
            },
            {
                backgroundColor: 'rgb(246,166,35)',
                borderColor: 'rgb(246,166,35)',
                pointBackgroundColor: '#fff',
                pointBorderColor: 'rgb(246,166,35)',
                pointHoverBackgroundColor: '#fff',
                pointHoverBorderColor: 'rgb(246,166,35)'
            }];
    }


    static direction = ['left', 'right', 'top', 'bottom'];
}

export class LineOption extends Option {
    constructor() {
        super();
        this.chartType = 'line';
    }

    static build(labels: any, datasets: any): LineOption {
        let lineOption = new LineOption();
        lineOption.options = {
            scaleShowVerticalLines: false,
            responsive: true,
            scales: {
                yAxes: [{
                    stacked: false
                }]
            },
            legend:{
                position:"bottom",
                labels: {
                    boxWidth: 15
                }
            }
        };
        lineOption.legend = true;
        lineOption.labels = labels;
        lineOption.datasets = datasets;
        return lineOption;
    }
    Datasets(datasets: any): LineOption {
        this.datasets = datasets;
        return this;
    }
    Labels(labels: any): LineOption {
        this.labels = labels;
        return this;
    }
    Options(options:any): LineOption {
        this.options = options;
        return this;
    }

    Legend(legend): LineOption {
        this.legend = legend;
        return this;
    }

    Colors(colors): LineOption {
        this.colors = colors;
        return this;
    }
}

export class BarOption extends Option {
    constructor(type?:string) {
        super();
        if (type === 'bar'
            || type === 'horizontalBar') {
            this.chartType = type;
        } else {
            this.chartType = 'bar';
        }
    }
    static build(type: string, labels: any, datasets: any): BarOption {
        let barOption = new BarOption(type);
        barOption.options = {
            scaleShowVerticalLines: false,
            responsive: true,
            legend:{
                position:"bottom",
                labels: {
                    boxWidth: 15
                }
            }
        };
        barOption.legend = true;
        barOption.labels = labels;
        barOption.datasets = datasets;
        return barOption;
    }
    Datasets(datasets: any): BarOption {
        this.datasets = datasets;
        return this;
    }
    Labels(labels: any): BarOption {
        this.labels = labels;
        return this;
    }
    Options(options:any): BarOption {
        this.options = options;
        return this;
    }

    Legend(legend): BarOption {
        this.legend = legend;
        return this;
    }

    Colors(colors): BarOption {
        this.colors = colors;
        return this;
    }

}



