    getUserMenu(userName: string): Promise<any[]> {
        console.log("getUserMenu service ... " + userName)
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                this.http.get("/auth-rest/menu/getUserMenu/" + userName)
                    .subscribe((data) => {
                        resolve(data.json().body)
                    });
            }, 25);
        });
    }

    private get(url): Promise<any> {
        return this.http.get(url).toPromise()
            .then(res => res.json())
            .catch(error => Promise.reject(error.message || error));
    }

