import {Component, Input, Output, EventEmitter, OnInit, OnChanges, DoCheck, SimpleChange} from '@angular/core';

import { NzModalService } from 'ng-zorro-antd';

import { YscDataTables } from './ysc-datatables.component';
import { YscDataTableColumn } from './ysc-datatables-config';

@Component({
  selector: 'ysc-datatables-search',
  template: `
    <span style="display:inline-block;width:100%;" (click)="search()">{{name}}</span>
  `,
  providers:[]
})
export class YscDataTablesSearch {

  @Input("datatables")
  datatables: YscDataTables;

  @Input()
  name;

  ngOnInit(): void {
      if (!this.datatables) {
          throw new Error("初始化失败，搜索组件没有对应的表格");
      }
      if (!this.name) {
          this.name = '查询';
      }
  }
  
  search() {
      this.datatables.reload();
  }

}


