import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import {CONFIG} from "../services/config";
import {DataService} from "../services/data.service";
import { HeaderService } from "../services/header-service";
import { CookieService } from 'ng2-cookies';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})

export class HeaderComponent implements OnInit {

    userMenus;
    // 用户名称
    private userPin;
    private userType;

    selectedOption;
    searchOptions = [];
    seller = '请输入关键字';
    sellerNo;

    isVisible = false;

    constructor(private dataService: DataService, private headerService: HeaderService,
                private _cookieService:CookieService) {
       // this.getUserInfo();
        /*console.log("sellerPin cookie:" + this._cookieService.get('sellerPin'));
        console.log("sellerName cookie:" + this._cookieService.get('sellerName'));

        console.log("current user cookie:" + this._cookieService.get('pin'));*/
    }

    setCookie(name:string,value:string){
        this._cookieService.set(name, value, null, '/',null, null);
    }

    searchChange(searchText) {
        // const query = encodeURI(searchText);
        // (this.jsonp.get(`https://suggest.taobao.com/sug?code=utf-8&q=${query}&callback=JSONP_CALLBACK`).map(res => res.json()) as Observable<Response>).subscribe((data: any) => {
        //   this.searchOptions = data.result;
        // });
        // this.searchOptions = this.test;
        this.getShopList(searchText);
    }

    dropSearch() {
        this.isVisible = !this.isVisible;
    }

    getShopList(sellerName=null) {
        this.dataService.getJson(CONFIG.URL.shoplist,{
            sellerName: sellerName
        })
            .then(data => {
                if (data.success) {
                    this.searchOptions = data.body;
                    /*if(this.searchOptions != null){
                        for(let seller in this.searchOptions){
                            if(this.searchOptions[seller].value == this.sellerNo){
                                this.seller = this.searchOptions[seller].label;
                            }
                        }
                    }*/
                }
            });
    }

    shopChange() {
        this.setCookie('sellerPin', this.selectedOption.value);
        this.setCookie('sellerName',this.selectedOption.label);
        this.seller = this.selectedOption.label;
        setTimeout(()=>{
            this.isVisible = false;
            window.location.reload();
        }, 1000);
        //  console.log(this.selectedOption);


    }

    ngOnInit() {
        this.getUserInfo();
        //this.seller = this._cookieService.get('sellerName');



        /*this.appsService.getUserMenu(this._cookieService.get('pin')).then(data => {
         console.log('fetching getUserMenu ...' + this._cookieService.get('pin'));
         this.userMenus = data as AppModel[];
         console.log("userMenus: " + this.userMenus);

         });*/
    }

    getUserMenu(userPin:string){
        this.headerService.getUserMenu(userPin).then(data => {
            this.userMenus = data;
            //console.log("userMenus: " + this.userMenus);

        });
    }

    getUserInfo() {
        this.dataService.getJson(CONFIG.URL.userInfo)
            .then(data => {
                if (data.success) {
                    this.userPin = data.body.userPin;
                    this.userType = data.body.userType;
                    if(this._cookieService.get('sellerPin') == null || this._cookieService.get('sellerPin')==''){
                        //console.log('set sellerNo from body:' + data.body.sellerNo);
                        this.sellerNo = data.body.sellerNo;
                        this.seller = data.body.sellerName;
                        this.setCookie('sellerPin',this.sellerNo);
                        this.setCookie('sellerName',this.seller);
                    }else{
                        //console.log('set sellerNo from cookie');
                        this.sellerNo = this._cookieService.get('sellerPin');
                        this.seller = this._cookieService.get('sellerName');
                    }

                    this.getShopList();
                    this.getUserMenu(this.userPin);
                    // console.info("=======userPin" + this.userPin);
                }
            });
    }

    logout() {
        this.dataService.getJsonNoCache(CONFIG.URL.logout)
            .then(data => {
                this._cookieService.deleteAll("/");
                this._cookieService.delete("sellerPin");
                this._cookieService.delete("sellerName");
                window.location.reload();
            });
    }



}



