import {Component, Input, Output, EventEmitter, OnInit, OnChanges, DoCheck, SimpleChange} from '@angular/core';

//<input [(ngModel)]="value" (blur)="updateEmit($event)" (keyup.enter)="updateEmit($event)" (keyup.esc)="updateEmit($event)"/>

@Component({
    selector: 'ysc-editable-dtcell',
    styles:[
      `
      .editable-dtcell {
        display:inline-block; 
        min-width: 50px;
        min-height: 18px;
        color:rgb(95, 167, 250);
        border: 1px solid;
        border-radius: 4px;
        cursor: pointer;
      }
      .editable-dtcell2 {
        display:inline-block; 
        min-width: 50px;
        min-height: 18px;
        color:rgb(95, 167, 250);
        border: 1px solid;
        border-radius: 4px;
        cursor: pointer;
        border-color: transparent;
      }
      .editable-dtcell2:hover {
        border: 1px solid #5fa7fa;
      }
      `
    ],
    template: `
    <div (dblclick)="onclick($event)">
        <span *ngIf="state === 0"  class="editable-dtcell">
            <nz-input [(ngModel)]="value" (nzBlur)="updateEmit($event)" [nzSize]="small" [nzAutosize]="true"></nz-input>
        </span>
        <span *ngIf="state === 1"  class="editable-dtcell2">
            {{value}}
        </span>
    </div>
  `,
    providers:[]
})
export class YscEditableDataTableCell {

    @Input("value")
    value: any;

    @Output()
    edited: EventEmitter<any> = new EventEmitter();

    private data: any;

    private type: "number"|"string"|"date" = "string";

    private state: CELL_STATE;

    onclick(event) {
        if (this.state == CELL_STATE.NON_EDITABLE) {
            this.state = CELL_STATE.EDITABLE;
        } else {
            this.updateEmit(event);
        }
        event.preventDefault();
    }

    updateEmit(event) {
        this.state = CELL_STATE.NON_EDITABLE;
        if (this.data != this.value) {
            this.edited.emit(this.value);
            this.data = this.value;
        }
    }

    ngOnInit() {
        this.state = CELL_STATE.NON_EDITABLE;
        this.data = this.value;
    }

}

enum CELL_STATE {
    EDITABLE,       // 0
    NON_EDITABLE    // 1
};


