import {Injectable} from '@angular/core';
import {LineOption} from "../overall-analysis-chart/overall-analysis-chart.option";
import {Http} from "@angular/http";
import {Metric} from "../model/il-overall-analysis.metric";
import "rxjs/add/operator/toPromise";
import {IlOverallAnalysisTable} from "../model/il-overall-analysis.table";
import {IlOverallAnalysisChart} from "../model/il-overall-analysis.chart";
import {EchartsLineOption} from "../model/il-overall-chart.option";

@Injectable()
export class OverallAnalysisService {

    private boardMetricUrl = "/app/v1/il/overall/board";

    private tableMetricUrl = "/app/v1/il/overall/table";

    private chartMetricUrl = "/app/v1/il/overall/chart";

    private echartMetricUrl = "/app/v1/il/overall/echart";

    constructor(private http: Http) {
    }

    buildTable(table : IlOverallAnalysisTable): IlOverallAnalysisTable{
        let ilOverallTable   = new IlOverallAnalysisTable();
        console.log(table);
        ilOverallTable.rowsThisYear = table.rowsThisYear;
        ilOverallTable.rowsLastYear = table.rowsLastYear;
        ilOverallTable.columnsLastYear = table.columnsLastYear;
        ilOverallTable.columnsThisYear = table.columnsThisYear;
        return ilOverallTable;
    }

    buildTrendLineOption(data: IlOverallAnalysisChart): LineOption {
        let line = new LineOption();
        line.labels = data.xAxis;
        line.datasets = [];
        let groupsLabel = {};
        for (let s in data.series) {
            //数据填充
            line.datasets.push({
                label: data.series[s].name,
                data: data.series[s].series,
                yAxisID: data.series[s].groupId,
                fill: false
            });

            //双坐标轴设置
            if (groupsLabel[data.series[s].groupId]) {
                groupsLabel[data.series[s].groupId] += ('/' + data.series[s].name);
            } else {
                groupsLabel[data.series[s].groupId] = data.series[s].name;
            }
        }
        ;
        let _yAxes = [];
        let iDirection = 0;
        for (let i in groupsLabel) {
            _yAxes.push({
                id: i,
                type: 'linear',
                position: LineOption.direction[iDirection++ % LineOption.direction.length],
                stacked: false,
                scaleLabel: {
                    display: true,
                    labelString: groupsLabel[i]
                }
            });
        }
        line.legend = true;
        line.options = {
            legend: {
                position: "bottom",
                labels: {
                    boxWidth: 15
                }
            },
            scales: {
                yAxes: _yAxes
            },
            scaleShowVerticalLines: false,
            responsive: true
        };
        return line;
    }


    getBoardMetrics(dept): Promise<Metric[]> {
        return this.http.get(this.boardMetricUrl + '?dept=' + dept).toPromise().
                            then(response => response.json().body.metrics as Metric[]).catch(this.handleError);
    }


    getTableMetrics(metricKey : string, dept): Promise<IlOverallAnalysisTable> {

        return this.http.get(this.tableMetricUrl + "?metricKey=" + metricKey + '&dept=' + dept).toPromise().
        then(response => response.json().body as IlOverallAnalysisTable).catch(this.handleError);
    }

    getChartMetrics(metricKey : string, dept): Promise<IlOverallAnalysisChart> {

        return this.http.get(this.chartMetricUrl + "?metricKey=" + metricKey + '&dept=' + dept).toPromise().
        then(response => response.json().body as IlOverallAnalysisChart).catch(this.handleError);
    }

    getEChartMetrics(metricKey : string, dept): Promise<any> {

        return this.http.get(this.echartMetricUrl + "?metricKey=" + metricKey + '&dept=' + dept).map(resp => resp.json()).toPromise();

    }


    private handleError(error: any): Promise<any> {
        console.error("an error ocurred :" + error);
        return Promise.reject(error.message || error);
    }

    buildOverAllYearLineOption(data: any): EchartsLineOption {
        let title = "";
        // let title = {
        //     text: '折线图堆叠'
        // }

        return EchartsLineOption.build(title,data.legend,data.xAxis,data.series,data.description);
    }


}



